import $T from '@/common/time.js';
export default {
	props: {
		item: Object,
		i: Number,
		pretime: [Number, String],
	},
	data() {
		return {
			uId: 1 //模拟当前用户user_id
		}
	},
	computed: {
		//本条气泡是否是用户本人的
		isSelf(){
			return this.item.user_id == this.uId;
		},
		//转化时间
		shortTime(){
			return $T.getChatTime(this.item.create_time, this.pretime);
		}
	},
}
